// PAGE BANNER
.banner{ 

    &-home{
        .slideSet{
            .slider{
                position: relative;
                background-position: center center;
                background-repeat: no-repeat;
                background-size: cover;
                height: 500px;

                &::after{
                    content: '';
                    position: absolute;
                    left: 0; top: 0;
                    z-index: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(#000000, 0.3);
                }
            }

            .flickity-page-dots{
                bottom: 50px;
            }
            
            .flickity-prev-next-button{
                &.previous{
                    left: 5%;
                }
                &.next{
                    right: 5%;
                }
            }
        }

        &_text{
            @include middle;
            z-index: 2;
            text-align: center;
            width: 100%;
            max-width: 700px;
            padding: 0 15px;

            h1{
                color: var(--color-white);
            }
            p{
                color: var(--color-white);
                font-size: 24px;
                font-family: $OpenSans;
                font-weight: 300;
            }
            .cta-primary{
                margin-top: 10px;
                padding: 10px 30px;
                font-size: 24px;
                font-weight: 700;
            }
        }
        
    }

    &-inner{
        position: relative;
        margin: 30px 0 50px;

        figure{           
            background-position: center top;
            background-repeat: no-repeat;
            background-size: cover;
            height: 400px;
            margin: 0;
        }                
    }

    @include media(992px, max){
        &-home{
            .slideSet{ 
                .slider{
                    height: 450px;
                }
                .flickity-page-dots{
                    bottom: 30px;
                }               
                .flickity-prev-next-button{
                    width: 60px;
                    height: 60px;
                    &:after {
                        font-size: 30px;
                    } 

                    &.previous{
                        left: 3%;
                    }
                    &.next{
                        right: 3%;
                    }
                }
            }
            &_text{
                p{
                    font-size: 22px;
                }
            }
        }
    }

    @include media(768px, max){
        &-home{
            .slideSet{ 
                .slider{
                    height: 350px;
                }                                
                .flickity-prev-next-button{
                    display: none;
                }
            }
            &_text{
                p{
                    font-size: 20px;
                }
                .cta-primary{
                    padding: 10px 25px;
                    font-size: 18px;
                    font-weight: 500;
                }
            }
        }

        &-inner{    
            figure{
                height: 300px;
            }                
        }
    }

    

}











