.cards{
    display:block;
    margin:0;
    padding:0; 
    height: 100%;   
    list-style:none;    

    [class*=card-]{
        position:relative;  
        display: block;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;                      

        footer{
            margin-top:auto;
        }
	}

    .card{
        &-feature{
            position: relative;
            overflow: hidden;
            
            &:hover{
                article{
                    opacity: 1;
                    transform: translateY(0);
                }
            }

            figure{
                margin: 0px;

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            article{
                background-color: var(--color-primary);
                color: var(--color-white); padding: 20px;
                position: absolute; left: 0; top: 0;
                transform: translateY(100%);
                height: 100%; width: 100%;
                opacity: 0; 
                @include transitions(all, 0.5s ease);

                h4{
                    color: var(--color-white);
                    font-weight: $bold;
                    line-height: 1.2;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
                p{
                    color: var(--color-white);
                    font-size: 18px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                }

                .group-btn{
                    display: flex;
                    gap: 14px;

                    [class*=cta-]{
                        @include border-radius(10px);
                    }
                }
            }
        }

        &-whyus{
            figure{
                border-bottom: 2px solid var(--color-faeee8);
                padding-bottom: 30px; margin-bottom: 30px;

                img{
                    height: 60px;
                    object-fit: cover;
                }
            }
        }

        &-product{
            figure{
                overflow: hidden;
                @include border-radius(14px);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            
            .type-price{
                display: flex;
                align-items: center;
                justify-content: space-between; 
                margin-bottom: 20px; 
                padding-bottom: 15px;
                border-bottom: 2px solid var(--color-d6b4a0);        

                .product-type{
                    font-size: 26px;
                    color: var(--color-ae7d5c);
                    font-weight: $sum-med;
                }
                .product-price{
                    font-size: 26px;
                    color: var(--color-ae7d5c);
                    font-weight: $bold;
                }
            }

            .color-size{
                display: flex;
                align-items: center;
                justify-content: space-between; 
                margin-bottom: 20px; 

                .product-color{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    .color-1{color: var(--color-cfb68b); font-size: 26px;}
                    .color-2{color: var(--color-a26844); font-size: 26px;}
                    .color-3{color: var(--color-c4996c); font-size: 26px;}

                    ul{
                        display: flex;
                        align-items: center;
                        gap: 10px;

                        li{
                            cursor: pointer;
                            font-size: 26px;

                            &.color-1{
                                color: var(--color-cfb68b);
                            }
                            &.color-2{
                                color: var(--color-a26844);
                            }
                            &.color-3{
                                color: var(--color-c4996c);
                            }
                        }
                    }
                }
                .product-size{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    font-size: 28px;
                    line-height: normal;

                    a{
                        color: var(--color-a26844);
                        font-weight: $med;
                        &:hover{
                            font-weight: $bold;
                        }
                    }
                }
            }
            .cta-primary{
                margin-top: 30px;
                padding: 15px;
                text-transform: uppercase;
                letter-spacing: 4px;
            }
        }

        &-testimonial{
            position: relative;
            padding: 60px 0 0;

            .testimonial-block{
                background-color: var(--color-primary);
                text-align: center;
                padding: 20px 20px 60px;
                box-shadow: 0 0px 20px 0 rgba(#000000, 0.1);
            }
            figure{
                width: 120px;
                height: 120px;
                overflow: hidden;
                margin: -60px auto 30px;
                @include border-radius(100%);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            article{
                h5{
                    color: var(--color-white);
                    font-size: 18px;
                }
                p{
                    color: var(--color-white);
                    font-size: 18px;
                    line-height: 1.5;
                }
            }          
        }  
        
        &-blog{
            background-color: var(--color-white);
            @include border-radius(14px);
            padding: 14px;

            figure{
                overflow: hidden;
                @include border-radius(14px); 

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            article{
                font-size: 16px;
                h5{
                    font-size: 20px;
                }
                p{
                    font-size: 16px;
                }
            }
            footer{
                display: flex;
                align-items: center;
                justify-content: space-between;

                .cta-primary{
                    padding: 8px 10px;
                    font-size: 14px;
                }
            }
        }
    }    
}
























