
/* For Mobile Menu */
.navToggle{
    margin-left: auto;
    display: none;
    padding: 0px;
    margin: 0px;
    border: 0px;
    outline: none;    
}
.mobnav-close{  
    img {
        -webkit-transition: 0.4s ease-out; 
        -moz-transition: 0.4s ease-out;
        transition: 0.4s ease-out;
        
        &:hover {
            -webkit-transform: rotateZ(182deg);
            -moz-transform: rotateZ(182deg);
            transform: rotateZ(182deg);
        }
    }
}

$bar-width: 32px;
$bar-height: 3px;
$bar-spacing: 8px; 
$bar-color: var(--color-primary);

.hamburger-menu {
    margin: auto;
    width: $bar-width;
    height: 21px;
    cursor: pointer;
    left: 8px;
    position: relative;
    display: block;
    
    .bar,
    .bar:after,
    .bar:before {        
        height: $bar-height;
    }
    .bar {
        position: relative;
        transform: translateY($bar-spacing);
        background: $bar-color;
        transition: all 0ms 300ms;
        width: 24px;
        display: block;
    
        &::before{
            width: 30px;
            content: "";
            position: absolute;
            right: 0;
            bottom: $bar-spacing;
            background: $bar-color;
            transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);            
        }

        &::after{
            width: 30px;
            content: "";
            position: absolute;
            right: 0;
            top: $bar-spacing;
            background: $bar-color;
            transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);            
        }
    }
    .mobileMenuOpen & .bar {
        background: transparent; 
    }
    .mobileMenuOpen & .bar::after {
        top: 0;
        transform: rotate(45deg);
        transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
    }
    .mobileMenuOpen & .bar::before {
        bottom: 0;
        transform: rotate(-45deg);
        transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);;
    }
}


.mobnav-grid{
    background-color: var(--color-primary);
    position: fixed;
    display: flex;
    flex-direction: column;
    padding: 30px 30px 100px;
    top: 0;
    right: 0;
    height: 100%;    
    z-index: 100;
    overflow: auto;       
    max-width: 400px;
    width: 100%;
    transition: 0.5s;
    transform: translateX(100%);

    .mobnav-close{
        margin-bottom: 10px;
    }

    &.open-mobile-nav{
        transform: translateX(0);
        box-shadow: 0px 0px 20px rgba(#ffffff, 0.6);         
    }    

    ul{
        width: 100%;
        padding: 0px;
        margin: 0px;
        list-style: none;

        li{                              
            line-height:1.4; 
            margin: 0;
            position: relative;
            border-bottom:1px solid rgba(#ffffff, 0.4);

            &:last-child{
                border-bottom: 0px;
            }            
            &>a{
                color: var(--color-white);
                display: block;
                padding: 10px 0;
                font-weight: $med;
                
                &:hover{
                    color: var(--color-white);
                }                
            }                        
            .menu-link{
                &:before{
                    position: absolute;
                    right: 0; top: 12px;
                    color: var(--color-white);                       
                    width: 20px; height: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 22px;
                    font-family: "Font Awesome 5 Pro";
                    font-weight: 400;  
                    content: "\f107";
                }                
            }
            &.active{
                .menu-link{
                    &:before{
                        content: "\f106";
                    } 
                }
            }

            .menu-info{
                li{                                           
                    margin: 0px !important;

                    &>a{                        
                        padding: 5px 10px;
                        font-weight: $regular;

                        &:hover{
                            color: var(--color-101010);
                        } 
                    }
                }
            }   
            
            ul{
                li{
                    border-bottom: 0px;
                }
            }
        }                     
    }
}