// Button Stylings for Cookies Noti bar

[class*=cta-]{    
	display: inline-block;
    text-align: center;
    text-decoration: none; 
    position: relative; 
    border: 0px; 
    outline: none;
    cursor: pointer;        
    padding: 10px 20px;
    white-space: nowrap;
    font-weight: $med;
    font-size: 18px;
    line-height: normal;
    @include border-radius(50px);
   
    letter-spacing: 1px;

    &.small{
        font-size: 14px;
        padding: 8px 16px;
        font-weight: $med;
    }

    &.full{
        display: block;
        width: 100%;
    }

    &.with-icon{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
    }

    &.with-shadow{
        box-shadow: 0 8px 12px 0 rgba($color: #39c074, $alpha: 0.5);
        
    }
}

.cta-{ 
    &primary{   
        background: var(--color-primary);        
        color: var(--color-white);         

        &:hover{
            background: var(--color-21a159);        
            color: var(--color-white);
        }        
    }     
    
    &border{
        border: 1px solid var(--color-white);
        color: var(--color-white);        

        &:hover{
            background-color: var(--color-white);
            color: var(--color-primary);
        } 
    }    
}

.text-link{
    color: var(--color-primary);

    &:hover{
        text-decoration: underline;
    }
}





 

