.gbl-header{
    nav.site-nav{
        width:auto;         
        
        .site-menu{
            padding: 0px;
            margin: 0px;
            list-style: none;
            display: flex;
            align-items: center;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 0 35px;
            justify-content: center;  
            
            li{
                &>a{
                    color: var(--color-text);
                    font-weight: $med;
                    text-transform: uppercase;

                    &:hover{
                        color: var(--color-hover);
                    }
                }
            }

            .sub-menu{                 
                font-size: 18px;                
                
                &>a{                    
                    padding: 15px 0;
                    display: block;  
                    position: relative;                     

                    &::after{
                        content: '\f078';                        
                        @include fontAwesome6;                                               
                        margin-left: 10px;
                        font-size: 16px;
                        position: relative;
                        top: 0px;
                    }
                }

                &:not(.has-mega-menu){
                    position: relative;

                    &>.dropdow-menu { 
                        left: 50%;
                        max-width: 250px;
                        min-width: 250px;
                        padding: 20px 0 0;  
                        text-transform: inherit;        
                    }
                }

                &:last-child{
                    .dropdow-menu{
                        transform: translate(-85%, 20px);
                    }
                    &:hover{
                        .dropdow-menu{
                            transform: translate(-85%, 0px);
                        }
                    }
                }

                &:hover{
                    &>a{
                        color: var(--color-primary);
                    }
                    .dropdow-menu{
                        //display: block; 
                        visibility: visible;
                        opacity: 1;
                        transform: translate(-50%, 0px); 
                    }
                    .fa-chevron-down {
                        @include rotateZ(-182deg);                        
                    }
                }

                &.hover{
                    .dropdow-menu{
                        display: block;
                        transition: transform 5s ease,;
                        transform: translate(-50%, 0px);
                    }
                }
            }
        }    
        
        .dropdow-menu {                        
            left: 50%;
            position: absolute;
            z-index: 3;
            padding: 40px 0 0   ; 
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;                       
            width: 100%;
            //display: none;
            visibility: hidden;
            opacity: 0;
            transition: transform .5s ease, opacity .5s ease;
            transform: translate(-50%, 20px);

            .container{
                display: block;
                max-width: 1300px;
                padding: 30px 15px;
            }

            .inner{
                max-width: 100%;
                padding:5px;
                margin: 0 auto;
                list-style: none;
                background-color: var(--color-primary);
                box-shadow: 0 0 6px 0px rgba(0,0,0,.3);
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;

                &>ul{
                    padding: 0px;
                    margin: 0px;                    

                    li{
                        list-style: none;
                        line-height: 18px;                        
                        margin: 4px 0;

                        &>a{
                            @include border-radius (5px);
                            padding:10px 15px;
                            display: block;                           
                            margin: 0px;
                            color: var(--color-white);                            

                            &:hover{
                                color: var(--color-text);
                            }
                        }
                    }
                }                                       
            }            
        }        
    } 
    
    @include media(1520px, max){
        nav.site-nav{
            .site-menu{                
                gap: 0 20px;

                .sub-menu{                 
                    font-size: 16px;   
                }
            }
        }
    }

    @include media(1350px, max){
        nav.site-nav{
            .site-menu{                
                gap: 0 10px;

                .sub-menu{                 
                    font-size: 16px;   
                }
            }
        }
    }

    @include media(1024px, max){
        nav.site-nav{
            display: none; 
        }

        .navToggle{
            display: block;
            position: absolute;
            right: 12px;
            background-color: $trans;
        }
    }
}












