// PAGE BANNER
.footer{     
    background-color: var(--color-white);
    padding: 60px 0 30px;

    .footer-logo{
        max-width: 200px;
        margin-bottom: 20px;
    }
    .footer-newsletter{
        width: 100%;
        max-width: 400px;
        margin-bottom: 30px;

        h3{
            font-weight: $med;
        }

        form{
            position: relative;

            .field{
                position: relative;
                border: 1px solid var(--color-1e1e1e);
                padding: 5px;
                @include border-radius(20px);
                display: flex;

                .form-control{
                    padding: 15px 20px;
                    border: 0px;

                    &:focus {
                        border-color: transparent; 
                        box-shadow: inherit;
                    }
                }
                .cta-primary{
                    @include border-radius(100px);
                    @include flex-width(54px);
                    height: 54px; padding: 5px;
                    font-size: 24px;
                }
            }
        }
    }
    .footer-link-wrap{
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        justify-content: space-between;  
        gap: 0 30px; 
        
        .footer-link{
            margin-bottom: 30px;
        }
    }    
    
    &_bottom{
        border-top: 1px solid #e3e3e3;
        padding: 20px 0 0;
        margin-top: 30px;
        text-align: center;
        color: var( --color-bdbdc6);
        display: flex;
        align-items: center;
        justify-content: space-between;

        a{
            color: var(--color-text);
            &:hover{
                color: var(--color-hover);
            }
        }

        p{
            margin: 0;
        }
    }

    .footer-link{
        ul{
            li{
                a{
                    color: var(--color-text);
                    padding: 8px 0;
                    display: block;
                    line-height: 1.2;

                    &:hover{
                        color: var(--color-hover);
                    }
                }
            }
        }
    }

    @include media(1200px, max){
        
    }

    @include media(768px, max){
        &_bottom{
            flex-direction: column;  
            
            p{
                padding-bottom: 10px;
            }
        }
    }
}












