// DIMENSIONS & GRID
// --------------------------------
@use "sass:math";

// Variables Declaration
// --------------------------------
:root {    
      /* Colors */
    --color-hover: #39c074;
    --color-primary: #39c074;    
    --color-secondary: #181818;
    --color-text: #181818; 

    --color-white: #ffffff;
    --color-black: #000000;
    --color-21a159: #21a159;
    --color-d1d1d1: #d1d1d1;
    

    --color-ff0800: #ff0800;
   } 

// FONT-WEIGHT - $MAIN
// --------------------------------
$slight: 100;
$light: 300;
$regular: 400;
$med: 500;
$sum-med: 600;
$bold: 700;
$strong: 700;
$weightBlack: 900;

$OpenSans: "Open Sans", serif;
$Raleway: "Raleway", sans-serif;


// FX
// --------------------------------
$shadow: 0px 0px 5px 0px #333;  // Global Box Shadow Style (if applicable)

$gutter:30px;
$pad:5px;
$mar:5px;
$trans:transparent; 




