.common-header{
    position: relative;
    margin-bottom: 50px;

    h6{
        padding-left: 60px;
        color: var(--color-primary);

        &:before{
            content: ''; left: 0;
            @include vertical-align;
            width: 40px; height: 2px;
            background-color: var(--color-primary);
        }
    }
}


.img-banner{
    min-height: 400px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
}

@include media(1024px, max){
    .common-header{       
        margin-bottom: 50px;
    
        h6{
            padding-left: 40px;
            margin-bottom: 10px;
    
            &:before{ width: 25px; }
        }
    }

    .img-banner{
        min-height: 300px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}




















