.slideSet{
    .slider {
        width: 100%;
        margin: 0px;     
        //counter-increment: slider;
    }    

    .flickity-prev-next-button{
        width: 80px;
        height: 80px;
        border-radius: 0;
        @include padding(0px);

        svg{
            display: none !important;
        }
    }
    .flickity-prev-next-button,
    .flickity-prev-next-button {                
        background-color: rgba(#ffffff, 0.5);
        @include border-radius(50%);
        
        &:after {
            color: var(--color-black); 
            font-size: 44px;
            @include fontAwesome6;
            @include middle;            
        }   
        &:focus {
            border: 0px;
            outline: none;
        } 
        
        &:hover{
            background-color: rgba(#ffffff, 0.8);
            @include border-radius(50%);
        }
    }
    .flickity-prev-next-button.previous {
        left: 0px;

        &:after{           
            content: "\f053";            
        }
    }
    .flickity-prev-next-button.next{
        right: 0px;
        &:after{
            content: "\f054";           
        }
    } 
    
    .flickity-page-dots{
        bottom: 50px;

        .dot {
            width: 30px;
            height: 8px;
            margin: 0 8px;
            background: var(--color-white);
            border-radius: 0;            
        }
    }
}

.groupSlider{
    @extend .slideSet;
    z-index:1;
    margin: 0 -10px;

    .slider {
        width: calc(90% - 1rem);
        padding: 0 10px;         
    }  

    .flickity-prev-next-button{
        display:none;
        top:25px;   
    }
    .flickity-prev-next-button.previous {
        left: inherit;
        right: 65px;        
    }
    .flickity-prev-next-button.next{
        right: 12px;  
    }

    @include media (768px, min){ 
        margin: 0 -24px;        
        .slider {
            width: calc(50%); 
            padding: 0 24px;                       
        }    
        .flickity-prev-next-button{
            display:inline-block;   
        }
    } 
    @include media (992px, min){ 
        .slider {
            width: calc(33.3333%);                       
        }
    } 
}















