.header-grid{
    background-color: var(--color-white);
}
.gbl-header {    
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
    position: sticky;
    top: 0;
    z-index: 9;  
    background-color: var(--color-white);


    .container{
        display:flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        
        &::after{
            content: none;
            display: inherit;
            clear: inherit;
        }                             
    }

    .brand-logo{
        max-width: 200px;
        display: block;
        margin-bottom: -75px;
        img{
            display: block;
        }
    }

    .menu-grid{
        //flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px 0;
        margin-left: auto;
        margin-right: 30px;
    }  
    
    .cta-primary{
        width: 120px;
        text-transform: uppercase;
    }
}



@include media(1024px, max){
    .gbl-header {
        .cta-primary{
            margin-right: 45px; 
        }
    }
}

@include media(600px, max){
    .gbl-header {
        flex-wrap: wrap;
        justify-content: space-between;

        .brand-logo{
            max-width: 100%;
            width: 100%;
            margin: 0 0 15px;

            img{
                max-width: 200px;
            }
        }

        .acc-head{
            background-color: var(--color-CACACA);
        }

        .cta-primary{
            margin-right: 0;
        }
    }
}






    







