.our-testimonials{
    margin: 100px 0 150px;

    header{
        max-width: 600px;
        margin: 0 auto 50px;

        h4{
            font-size: 37px;
            font-weight: $bold;
            text-transform: uppercase;
        }
        p{
            font-size: 24px;
            font-weight: $light;
            line-height: 1.4;
        }
    }

    .cards{
        .card-testimonial{
            padding: 100px 20px;
        }
    }

    .groupSlider{
        .slider {
            &.is-selected{
                .cards{
                    .card-testimonial{
                        padding: 60px 0 0;

                        .testimonial-block{
                            background-color: var(--color-white);

                            article{
                                h5{
                                    color: var(--color-text);
                                    font-size: 24px;
                                }
                                p{
                                    color: var(--color-text);
                                    font-size: 24px;
                                    line-height: 1.5;
                                }
                            }
                        }
                    }
                }
            }
        }

        .flickity-page-dots{
            display: none;
        }
        .flickity-prev-next-button{
            top: inherit;
            bottom: -100px;
        }
        .flickity-prev-next-button,
        .flickity-prev-next-button {                
            background-color: rgba(#ffffff, 1); 
            width: 60px;
            height: 60px;           
            
            &:after {
                color: var(--color-text); 
                font-size: 34px;                           
            }            
            &:hover{
                background-color: rgba(#ffffff, 0.6);
                @include border-radius(50%);
            }
        }
        .flickity-prev-next-button{
            &.previous{
                left: 45%;
                right: inherit;
            }
            &.next{
                right: 45%;
            }
        }
    }  

    @include media(1200px, max){
        .groupSlider{            
            .flickity-prev-next-button{
                &.previous{
                    left: 42%;
                }
                &.next{
                    right: 42%;
                }
            }
        }         
    }

    @include media(768px, max){
        margin: 50px 0 100px;

        .cards{
            .card-testimonial{
                padding: 50px 0px;

                article{
                    h5{
                        font-size: 20px;
                    }
                    p{
                        font-size: 18px;
                    }
                }
            }
        }

        .groupSlider{
            .slider {
                &.is-selected{
                    .cards{
                        .card-testimonial{
                            padding: 50px 0px;
                            
                            .testimonial-block{
                                article{
                                    h5{
                                        font-size: 20px;
                                    }
                                    p{
                                        font-size: 18px;
                                    }
                                }
                            }
                        }
                    }
                }  
            }          
            .flickity-prev-next-button{
                display: block;
                &.previous{
                    left: 34%;
                }
                &.next{
                    right: 34%;
                }
            }
        }
    } 

    @include media(580px, max){
        .groupSlider{            
            .flickity-prev-next-button{
                display: block;
                &.previous{
                    left: 10%;
                }
                &.next{
                    right: 10%;
                }
            }
        }
    }  
}