body {    
    font-size: 16px;
    font-family: $Raleway;
    font-weight: $regular;
    color: var(--color-text);
    background-color: var(--color-f3eeea);  
    padding: 0px;
    margin: 0px;   
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;  
    -webkit-text-size-adjust: 100%; 
    scroll-behavior:smooth;      
}
#window{
    width: 100%;
    min-height: 100%;
    //overflow-x: hidden;
    @include transition(transform, .4s); 
    @include selfClearing; 
        
    //transition: transform .5s cubic-bezier(.075,.82,.165,1);    
}
.body-wrap{
    //min-height:400px;    
    width:100%;
    margin-left: auto;
    margin-right: auto;
    @include selfClearing;    
}
p{    
    line-height: 26px;
    margin-bottom: 20px; 
    color: var(--color-text);

    @include media(992px, min){
        margin-bottom: 25px;
    }
}

a{
    text-decoration:none;     
    color: var(--color-primary);
    @include transitions(all, 0.4s ease);

    &:hover{
        text-decoration:none;
        color: var(--color-hover);
        //@include transitions(all .3s 0s ease-in-out);  
    }
}
hr {
    margin-top: 16px;
    margin-bottom: 16px;
    border: 0;
    opacity: 1;
    border-top: 1px solid var(--color-border);
}

img, svg {
    vertical-align: middle;
    max-width: 100%;
    height: auto;
}

figure {
    display: block;    
    position: relative;
    overflow: hidden;
    @include selfClearing;
}
a:focus,
:focus-visible {
    outline: none;
}
ul{
    padding: 0px;
    margin: 0px;
    list-style: none;
    li{
        padding: 0px;
        margin: 0px;
    }
}

.none{
    display: none;
}
.hidescroll {
    overflow: hidden;
}

.container{
    max-width:1600px; 
    width:100%;  
    padding-left:12px;
    padding-right:12px;   
    margin: 0 auto;    
    position: relative;
    @include selfClearing;  

    &.full{
        max-width:100%;   
    } 
    &.larg{
        max-width:1190px;   
    } 
    &.med{
        max-width:800px;   
    }    
}

.gbl{
    margin-top: 50px;
    margin-bottom: 50px;       
    &-top{
        &-50{margin-top: 50px;}
    }
    &-bottom{
        &-50{margin-bottom: 50px;}
    } 

    &-pad{
        padding-top:80px;
        padding-bottom:80px;
    }
    
    @include media(992px, max){
        margin-top: 60px;
        margin-bottom: 60px;
    }
}

// Heading sizes 
h1, h2, h3, h4, h5, h6 {     
    color: var(--color-text);
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    position: relative;
    line-height: normal; 
    font-family: $OpenSans;  
    font-weight: $regular; 
}
h1{
    font-size: 75px;
    line-height: 80px;   
}
h2{
    font-size: 55px;
    line-height: 70px;   
    font-weight: $bold;
    color: var(--color-primary);
}
h4{
    font-size: 35px;  
    font-weight: $sum-med;
}
h5{
    font-size: 25px; 
    font-weight: $sum-med; 
}
h6{
    font-size: 22px; 
    font-weight: $sum-med;
    position: relative; 
}

@include media(1024px, max){
    h1{
        font-size: 55px;
        line-height: 65px;   
    }
    h2{
        font-size: 40px;
        line-height: 55px;   
    }
}
@include media(768px, max){    
    h1{
        font-size: 45px;
        line-height: 55px;   
    } 
    h2{
        font-size: 36px;
        line-height: 44px;   
    }
    h6{
        font-size: 18px; 
    }
} 

.text{
    &-14{
        font-size: 14px;
    }
}

.bg-{
    &white{
        background-color: var(--color-white);
    }
    &grey{
        background-color: #fafafa;
    }
}

.for-mobile {
    display: none;
} 

@include media(992px, max){     
    .for-desktop {
        display: none !important;
    }  
    .for-mobile {
        display: block;
    }        
}


img,
video{
    &.cover{
        -o-object-fit: cover;
        object-fit: cover;
        @include middle;
        min-width:100%;
        min-height:100%;
    }
}

@media (min-width:768px) {
    .modal-small {max-width:450px; width:100%;}
    .modal-sm {max-width:650px; width:100%;}
    .modal-md {max-width:850px; width:100%;}
    .modal-lg {max-width:1050px; width:100%;}
}

*.mt-{
