    &-10{margin-top:2*$mar;}
    &-20{margin-top:4*$mar;}
    &-30{margin-top:6*$mar;}
    &-40{margin-top:8*$mar;}
    &-50{margin-top:10*$mar;}
    &-60{margin-top:12*$mar;}
